#game-board {
    margin: 20px auto;
    width: 400px;
    height: 400px;
    background-color: #000;
    position: relative;
    border: 4px solid #fff; /* Change this to your desired border color */
  }
  
  .snake {
    background-color: green;
    position: absolute;
    width: 20px;
    height: 20px;
  }
  
  .food {
    background-color: red;
    position: absolute;
    width: 20px;
    height: 20px;
  }